import React, { Component } from "react";

export default class Product extends Component {
  render() {
    return (
      <>
        <div className="product m-auto text-center my-4">
          <div className="product-cart">
            <div>
              <img
                src={this.props.product.image_url}
                className="img-fluid cart-img"
              />
              <small className="text-muted m-0 d-block">{this.props.product.name}</small>
            </div>

            <div className="w-50 cart-btns">
              <button
                onClick={() => this.props.onIncreament(this.props.product)}
                className="btn cart-icon btn-sm"
              >
                +
              </button>

              <div>{this.props.product.count}</div>

              <button
                onClick={() => this.props.onDecreament(this.props.product)}
                className="btn cart-icon btn-sm"
              >
                -
              </button>
            </div>

            <div
              onClick={() => this.props.onDelete(this.props.product)}
              className="btn btn-danger btn-sm"
            >
              <i className="fas fa-trash-alt"></i>
            </div>
          </div>
        </div>
      </>
    );
  }
}
