import React, { Component } from "react";
import Product from "./../Product/Product";

class ShoppingCart extends Component {
  render() {
    const { products, onDecreament, onIncreament, onDelete, onReset } =
      this.props;

    return (
      <>
        <div className="text-center mb-5 pt-5">
          <div className="container">
            <div className="cart-head mt-5">
              <p className="text-muted mb-1">CART</p>
              <h2 className="text-orange mb-5">Your Food Cart</h2>
            </div>

            {products.map((prdct) => (
              <Product
                key={prdct.id}
                product={prdct}
                onDelete={onDelete}
                onIncreament={onIncreament}
                onDecreament={onDecreament}
              />
            ))}
            <button onClick={onReset} className="btn btn-secondary m-2">
              Reset
            </button>
          </div>
        </div>
      </>
    );
  }
}

export default ShoppingCart;
